$(document).ready(function(){
    $(window).scroll(function(e){
        let windowScroll = $(window).scrollTop();
        if(windowScroll > 0){
            $('.navbar').addClass('navbar-light').addClass('bg-light').removeClass('navbar-dark').removeClass('bg-dark');
            $('.img-logo').attr('src','images/logo-black.svg');
        }else{
            $('.navbar').removeClass('navbar-light').removeClass('bg-light').addClass('navbar-dark').addClass('bg-dark');
            $('.img-logo').attr('src','images/logo-white.svg');
        }
    })

    var s = skrollr.init();
});